body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 0 20px;
  font-family: 'Gloria Hallelujah', cursive;
}
.game {
  margin: auto;
}
.field {
  width:428px;
  height: 430px;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-content: center;
          align-content: center;
  color: rgb(255, 38, 38);
}
.field__cell {
  display: inline-block;
  width:140px;
  height:140px;
  line-height:140px;
  text-align: center;
  border:2px solid rgb(34, 34, 34);
  cursor: pointer;
  font-size: 90px;
  background-color: #333;
}
.field__cell:nth-child(7), .field__cell:nth-child(8), .field__cell:last-child {
  border-bottom: none;
}
.field__cell:first-child, .field__cell:nth-child(4), .field__cell:nth-child(7) {
  border-left: none;
}
.field__cell:nth-child(3), .field__cell:nth-child(6), .field__cell:nth-child(9) {
  border-right: none;
}
.field__cell:first-child, .field__cell:nth-child(2), .field__cell:nth-child(3) {
  border-top: none;
}
.result, .restart-btn {
  font-size: 32px;
  padding: 10px;
  text-align: center;
  margin: 10px 0;
  width: 408px;
  height: 40px;
  line-height: 40px;
  color: rgb(255, 38, 38);
  background: rgb(51, 51, 51);
}
.restart-btn:hover {
  background: rgba(51,51,51, .9);
  cursor:pointer;
}
.current-turn {
  margin-bottom: 10px;
  width: 428px;
  height: 40px;
  line-height: 40px;
  font-size: 30px;
  text-align: center;
}
.current-turn span {
  color: rgb(255, 38, 38);
}
